.App-requestform{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;

  padding: 0.3em;
  border-bottom: solid 1px silver;
  background: lightgrey;
}
.App-requestform>aside{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 2;
      flex-grow: 2;
  -ms-flex-pack: end;
      justify-content: flex-end;
  height: auto;
}
.App-requestform select,
.App-requestform input{
  height: 21px;
}
@media only screen
  and (max-width: 480px)
  and (orientation: portrait) {
  input{
    width: 100%;
  }
}



