.App-footer{
  color: white;
  min-height: 2em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  width: 100%;
  -ms-flex-positive: 0;
      flex-grow: 0;
  background: dimgrey;
  background-image: -webkit-repeating-linear-gradient(45deg, transparent, transparent 3px, grey 3px, grey 6px);
  background-image: -o-repeating-linear-gradient(45deg, transparent, transparent 3px, grey 3px, grey 6px);
  background-image: repeating-linear-gradient(45deg, transparent, transparent 3px, grey 3px, grey 6px);
}

.App-footer>aside{
  height: 2em;
  line-height: 2em;
  -ms-flex-positive: 2;
      flex-grow: 2;
  text-align: left;
  padding-left: 1em;
}

.App-button-icon{
  margin: 0 0.5em;
  font-weight: bold;
}

@media only screen
  and (max-width: 300px){
  .App-button-label{
    display: none;
  }
}



